$clock-size: 600px;
$background-clock-color: #f6ddcc;

$second: 1s;
$minute: $second * 60;
$hour: $minute * 60;

$setTimeHour: 0;
$setTimeMinute: 0;
$timeShiftHour: $setTimeHour * $hour;
$timeShiftMinute: $setTimeMinute * $minute;

$second-hand-color: #f39c12;
$second-hand-height: 230px;
$second-hand-width: 10px;

$minute-hand-color: #7d3c98;
$minute-hand-height: 170px;
$minute-hand-width: 20px;
